h1 {
  font-weight: bolder;
  font-size: 3em;
  width: 100%;
  margin-bottom: 5px;
}

hr {
  width: 100px;
  color: whitesmoke;
  border: 1px solid whitesmoke;
  margin-bottom: 25px;
}

br {
  width: 100%;
}

.text-light {
  color: whitesmoke;
}

.text-dark {
  color: #212121;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-bold {
  font-weight: bold;
}

.text-center {
  text-align: center;
}

.App {
  font-family: 'lato', 'sans-serif';
  background-image: linear-gradient(to right top, #ffa000, #ff7a46, #ff5581, #ff4cc2, #eb68ff);
  background-size: cover;
  background-attachment: fixed;
}

.hero {
  height: 80vh;
}

.flex-center {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-content: center;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.flex-center-form {
  width: 250px;
  display: flex;
  flex-flow: row wrap;
  align-content: center;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.bottom {
  bottom: 0;
  position: absolute;
  background-color: whitesmoke;
  box-shadow: 0 -3px 6px rgba(0,0,0,.2);
  font-size: 1em;
  height: 20vh;
  width: 100vw;
  padding-bottom: 50px;
}

.icon-small {
  width: 20px;
  vertical-align: middle;
  margin-bottom: 3px;
  margin-right: 10px;
  user-select: none;
}

#password-header {
  margin-top: 20px;
  width: 100%;
  margin-bottom: 0;
}

@media only screen and (max-width: 1000px) {
  .App {
    font-size: 0.8em;
  }

  .bottom {
    height: 10vh;
  }

  .hero {
    height: 90vh;
  }
}

@media only screen and (max-width: 374px) {
  .App {
    font-size: 0.7em;
  }
}