.modal {
    width: 100vw;
    position: absolute;
    background-color: #212121;
    overflow-y: hidden;
    margin: 0;
    height: 60px;
    z-index: 1;
    top: -60px;
    height: 60px;
    user-select: none;
}

.visible {
    transition: 0.5s;
    top: 0;
}

.hidden {
    transition: 0.5s;
    height: 0px;
}

.hidden .close-button {
    display: none;
    transition: 0.5s;
}

.close-button {
    background-color: rgba(238, 238, 238, 0.6);
    border: none;
    border-radius: 20px;
    margin: 20px 10px;
    width: 15px;
    height: 15px;
    float: left;
}

.close-button img {
    width: 15px;
    height: 15px;
    margin: 0 auto;
    margin-bottom: 1px;
}
