body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-weight: bolder;
  font-size: 3em;
  width: 100%;
  margin-bottom: 5px;
}

hr {
  width: 100px;
  color: whitesmoke;
  border: 1px solid whitesmoke;
  margin-bottom: 25px;
}

br {
  width: 100%;
}

.text-light {
  color: whitesmoke;
}

.text-dark {
  color: #212121;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-bold {
  font-weight: bold;
}

.text-center {
  text-align: center;
}

.App {
  font-family: 'lato', 'sans-serif';
  background-image: linear-gradient(to right top, #ffa000, #ff7a46, #ff5581, #ff4cc2, #eb68ff);
  background-size: cover;
  background-attachment: fixed;
}

.hero {
  height: 80vh;
}

.flex-center {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-content: center;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.flex-center-form {
  width: 250px;
  display: flex;
  flex-flow: row wrap;
  align-content: center;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.bottom {
  bottom: 0;
  position: absolute;
  background-color: whitesmoke;
  box-shadow: 0 -3px 6px rgba(0,0,0,.2);
  font-size: 1em;
  height: 20vh;
  width: 100vw;
  padding-bottom: 50px;
}

.icon-small {
  width: 20px;
  vertical-align: middle;
  margin-bottom: 3px;
  margin-right: 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

#password-header {
  margin-top: 20px;
  width: 100%;
  margin-bottom: 0;
}

@media only screen and (max-width: 1000px) {
  .App {
    font-size: 0.8em;
  }

  .bottom {
    height: 10vh;
  }

  .hero {
    height: 90vh;
  }
}

@media only screen and (max-width: 374px) {
  .App {
    font-size: 0.7em;
  }
}
.display p {
    background-color: lightgrey;
    color: #212121;
    opacity: 0.75;
    font-weight: bold;
    padding: 10px 20px;
    border-radius: 5px;
    box-shadow: 0 6px 12px rgba(0,0,0,0.2);
}
.container {
    padding: 15px 20px;
    background-color: whitesmoke;
    text-transform: uppercase;
    border-radius: 50px;
    text-decoration: none;
    text-align: left;
    display: inline-block;
    box-shadow: 0 3px 6px rgba(0,0,0,.2);
    max-width: 150px;
    margin: 2px;
    margin-bottom: 12px;
}

.icon-small {
    width: 20px;
    vertical-align: middle;
    margin-bottom: 3px;
    margin-right: 10px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.input {
    margin-left: 20px;
    border: 0;
    background-color: transparent;
    font-size: 1em;
    font-weight: bold;
    text-align: center;
}

.input:focus {
    outline: none;
}

.input:invalid {
    -webkit-animation: shake .5s;
    animation: shake .5s;
}

.small {
    max-width: 35%;
}

.wide {
    width: 100%;
}

.button {
    padding: 15px 40px;
    text-transform: uppercase;
    border-radius: 50px;
    margin: 10px;
    text-decoration: none;
    display: inline-block;
    font-size: .8em;
    text-align: center;
    box-shadow: 0 3px 6px rgba(0,0,0,.2);
}

.button:hover {
    box-shadow: 0 6px 12px rgba(0,0,0,.2);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: 0.3s;
}

.button:focus {
    outline: none;
}

.primary {
    color: white;
    background-color: #212121;
    border: 2px solid #212121;
}

.outline-light {
    color: white;
    background-color: transparent;
    border: 2px solid white;
    box-shadow: none;
}

.outline-dark {
    color: #212121;
    background-color: transparent;
    border: 2px solid #212121;
    box-shadow: none;
}

.outline-light:hover, .outline-dark:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: 0.3s;
    box-shadow: none;
}

.error {
    border: 2px solid red;
    -webkit-animation: shake 200ms 2 linear;
            animation: shake 200ms 2 linear;
    margin: 0px;
    margin-bottom: 10px;
  }
  
  @-webkit-keyframes shake {
    0% {
      -webkit-transform: translate(6px, 0);
              transform: translate(6px, 0);
    }
    50% {
      -webkit-transform: translate(-6px, 0);
              transform: translate(-6px, 0);
    }
    100% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
    }
  }
  
  @keyframes shake {
    0% {
      -webkit-transform: translate(6px, 0);
              transform: translate(6px, 0);
    }
    50% {
      -webkit-transform: translate(-6px, 0);
              transform: translate(-6px, 0);
    }
    100% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
    }
  }
.modal {
    width: 100vw;
    position: absolute;
    background-color: #212121;
    overflow-y: hidden;
    margin: 0;
    height: 60px;
    z-index: 1;
    top: -60px;
    height: 60px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.visible {
    transition: 0.5s;
    top: 0;
}

.hidden {
    transition: 0.5s;
    height: 0px;
}

.hidden .close-button {
    display: none;
    transition: 0.5s;
}

.close-button {
    background-color: rgba(238, 238, 238, 0.6);
    border: none;
    border-radius: 20px;
    margin: 20px 10px;
    width: 15px;
    height: 15px;
    float: left;
}

.close-button img {
    width: 15px;
    height: 15px;
    margin: 0 auto;
    margin-bottom: 1px;
}

