.container {
    padding: 15px 20px;
    background-color: whitesmoke;
    text-transform: uppercase;
    border-radius: 50px;
    text-decoration: none;
    text-align: left;
    display: inline-block;
    box-shadow: 0 3px 6px rgba(0,0,0,.2);
    max-width: 150px;
    margin: 2px;
    margin-bottom: 12px;
}

.icon-small {
    width: 20px;
    vertical-align: middle;
    margin-bottom: 3px;
    margin-right: 10px;
    user-select: none;
}

.input {
    margin-left: 20px;
    border: 0;
    background-color: transparent;
    font-size: 1em;
    font-weight: bold;
    text-align: center;
}

.input:focus {
    outline: none;
}

.input:invalid {
    -webkit-animation: shake .5s;
    animation: shake .5s;
}

.small {
    max-width: 35%;
}

.wide {
    width: 100%;
}

.button {
    padding: 15px 40px;
    text-transform: uppercase;
    border-radius: 50px;
    margin: 10px;
    text-decoration: none;
    display: inline-block;
    font-size: .8em;
    text-align: center;
    box-shadow: 0 3px 6px rgba(0,0,0,.2);
}

.button:hover {
    box-shadow: 0 6px 12px rgba(0,0,0,.2);
    transform: scale(1.05);
    transition: 0.3s;
}

.button:focus {
    outline: none;
}

.primary {
    color: white;
    background-color: #212121;
    border: 2px solid #212121;
}

.outline-light {
    color: white;
    background-color: transparent;
    border: 2px solid white;
    box-shadow: none;
}

.outline-dark {
    color: #212121;
    background-color: transparent;
    border: 2px solid #212121;
    box-shadow: none;
}

.outline-light:hover, .outline-dark:hover {
    transform: scale(1.05);
    transition: 0.3s;
    box-shadow: none;
}

.error {
    border: 2px solid red;
    animation: shake 200ms 2 linear;
    margin: 0px;
    margin-bottom: 10px;
  }
  
  @keyframes shake {
    0% {
      transform: translate(6px, 0);
    }
    50% {
      transform: translate(-6px, 0);
    }
    100% {
      transform: translate(0, 0);
    }
  }